.movie-card{max-width:322px;margin-top:25px;position:relative}.movie-card__description-container{display:flex;justify-content:space-between;margin:22px 0px 0px 0px;align-items:center}.movie-card__description{margin:0px;color:#fff}.movie-card__description--title{font-size:18px}.movie-card__description--production-year{border:rgba(151,151,151,.5254901961) 1px solid;border-radius:4px;padding:5px 8px}.movie-card__description--genre{margin:8px 0px 0px 0px;opacity:50%}.movie-card__context-menu{position:absolute;display:inline-block;margin-left:auto;background-color:#2a202d;border-radius:50%;width:36px;height:36px;top:15px;right:15px}.movie-card__popupmenu_container{position:absolute;top:15px;right:15px}.movie-card__image{width:322px;height:455px}.movie-card__image-container{background-color:var(--empty-image-color);margin:0px;width:322px;height:455px;cursor:pointer}.icon-more-vertical::before{content:"";color:#fff;display:block;font-size:32px;padding-top:1px;padding-left:2px}