.modal-base{position:fixed;left:0px;top:0px;height:100vh;width:100vw;background-color:rgba(35,35,35,.911);backdrop-filter:blur(12.2323px);color:#fff}.modal-base__logo{padding:40px 0px 0px 60px;position:absolute}.modal-base__window-container{display:flex;justify-content:center;align-items:center;height:100%}.modal-base__window{display:flex;flex-direction:column;background-color:#232323}.modal-base__close-element{align-self:flex-end;margin:10px 10px 0px 0px;font-size:20px}.modal-base__heading{display:flex;text-transform:uppercase;font-size:40px;margin:10px 0px 0px 60px}.modal-base__children-container{display:flex;flex-wrap:wrap;margin:35px 60px 40px 60px;min-width:550px;min-height:100px}.icon-close{color:#fff}.icon-close:hover::after{color:#f65261}.icon-close ::after{content:"";display:block}