.drop-down-menu{display:flex;flex-direction:column;background-color:#232323;color:#fff;font-weight:500;font-size:16px;width:190px;border-radius:4px;user-select:none}.drop-down-menu__cross{margin:8px 8px 0px auto;cursor:pointer;font-size:12px}.drop-down-menu__item-container{margin:8px 0px;padding:0px}.drop-down-menu__item{height:35px;list-style-type:none;line-height:35px;padding-left:25px}.drop-down-menu__item:hover{background-color:#f65261;font-weight:600;cursor:pointer}.icon-close::after{content:"";color:#fff;display:block}