.sort-control{color:#fff;display:flex;align-items:center;text-transform:uppercase;font-size:20px}.sort-control__text{margin:0px}.sort-control__text--sorting{color:#a8a8a8;margin:0px 13px 0px 30px}.sort-control__select-container{position:relative;align-items:center}.sort-control__select{border:none;background-color:rgba(0,0,0,0);color:#fff;font-size:20px;appearance:none;padding-right:25px}.sort-control__select:focus{outline:none;border:none}.sort-control__select option{background-color:#232323;font-size:20px;border:none}.sort-control__select option:hover{box-shadow:0 0 10px 100px #f65261 inset}.icon-arrow-down{position:absolute;right:17px;top:-3px}.icon-arrow-down::before{content:"";display:inline-block;width:0;height:0;color:#f65261;font-size:7px}