.drop-down-input{position:relative}.drop-down-input__select-container{position:relative}.drop-down-input__select{width:100%;appearance:none}.drop-down-input__appearance{position:absolute;right:40px;top:20px}.drop-down-input__appearance::before{content:"";display:inline-block;width:0;height:0;color:#f65261;font-size:16px}.drop-down-input__appearance.rotated{right:15px;transform:rotate(180deg)}.drop-down-input__content-container{position:absolute;left:0;right:0;top:0;bottom:0}.drop-down-input__content{z-index:15;position:absolute;display:block;width:calc(100% - 32px);outline:#f65261 2px solid;background-color:#232323;border:none;border-radius:4px;padding:18px 16px}.drop-down-input__content label{display:block}