.movie-details{background-color:#232323;max-height:600px;padding:40px 60px 30px 60px}.movie-details__icon--search::before{content:"";color:#f65261;font-size:15px}.movie-details__icons-container{display:flex;justify-content:space-between}.movie-details__info-container{display:flex;margin-top:30px}.movie-details__image-container{width:323px;max-height:486px}.movie-details__image{background-size:cover;background-position:center;display:flex;justify-content:center;align-items:center;width:inherit}.movie-details__heading-container{display:flex}.movie-details__details-container{margin-left:50px}.movie-details__details-information--title{display:inline-flex;color:#fff;font-size:40px;margin:0px}.movie-details__details-information--score{display:inline-flex;color:#fff;justify-content:center;width:50px;height:50px;align-items:center;border:1px solid #fff;border-radius:50%;font-size:20px;align-self:center;margin:0px 0px 0px 25px}.movie-details__details-information--genre{color:#fff;opacity:.5;margin:9px 0px 30px 0px}.movie-details__details-information--release-date{display:inline-block;color:#f65261;margin:0px}.movie-details__details-information--duration{display:inline-block;color:#f65261;margin:0px 0px 0px 50px}.movie-details__details-information--description{color:#fff;opacity:.5;margin:30px 0px 0px 0px}.icon-search{transform:rotateY(180deg);cursor:pointer}.icon-search::before{content:"";color:#f65261;font-size:30px}